@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);

/* Google font include here  */

/* Site common css starts from here  */
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
body{
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
}
a{
    text-decoration: none;
    color: black;
}
/* Root color  */
:root {
    --green: #27ae60;
    --text-color: #192a56;
    --black-color: #080808;
    --border: .1rem solid rgba(0,0,0,.2);
    --box-shadow: 1px 1px 6px #77767654;
  }
  
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Header css styel start from here  */
section .header {
    background: #fff;
    font-family: 'Poppins';
    box-shadow: var(--box-shadow);
    padding: 10px 0px;
}

.siteLogo .logo {
    color: var(--green);
    font-size: 23px;
    font-weight: 700;
    text-decoration: none;
}
.menusOfMenubar{
    font-size: 18px;
    text-decoration: none;
    color: var(--text-color);
    font-size: 20px;
    margin: 5px 10px;
    font-weight: 600;

}
.menusOfMenubar:hover{
    color: var(--green);
    transition: .3s;

}

.active{
    color: black;
}

span.meni-icon{
    color: var(--green);
    font-size: 20px;
    margin: 5px 10px;
}
button.navbar-toggler.collapsed {
    color: var(--green);
    border: 1px solid var(--green);
}
span.navbar-toggler-icon {
    color: var(--green)!important;
    font-size: 25px;
}
/* Header css style ends to here*/
.foodCard{
    box-shadow: var(--box-shadow);
    background-color: #fff;
    margin: 25px 0px;
} 
span.saleBadge {
    color: var(--text-color);
    font-size: 20px;
    font-weight: 600;
    background: var(--green);
    padding: 5px 8px;
    position: relative;
    left: -130px;
    border-radius: 40px;
    top: 10px;
}
.foodCard img{
    margin-top: 10px;
    width: 200px;
    height: 200px;
    border-radius: 100%;
    cursor: pointer;
}

.foodCard h3.foodTitle{
    font-size: 22px;
    font-weight: 600;
    color: var(--text-color);
    margin-top: 15px;
    margin-bottom: 0px;
}
.foodCard span.regularPrice{
color: rgb(221, 129, 129);
text-decoration: line-through;
font-weight: 500;
font-size: 20px;
margin-right: 10px;
}
.foodCard span.salePrice{
color: var(--green);
font-weight: 700;
font-size: 23px;
}
.foodCard button.viewBtn{
    color: white;
    background-color: var(--green);
    padding: 10px 15px;
    width: 100%;
    margin-bottom: 0px;
    border: 0px;
    border-radius: 0px;
    font-size: 20px;
    margin-top: 10px;
}

.foodCard .foodRatting{
color: var(--green);
font-size: 19px;
margin: 5px 0px;
}
/* This shop page and shop page css style here  */
section .shopSection{
    margin-top: 0px;
    background: rgb(247 247 247);
    padding: 40px 0px;
    font-family: 'Poppins';
}
.totalData{
    text-align: left;
    font-size: 20px;
    font-weight: 600;
}
.searchIput{
    color: var(--green);
    padding: 5px 10px;
    font-size: 18px;
    border: 1px solid var(--green);
    border-radius: 5px;
    outline: none;
    width: 100%;
}
.singleFood {
    background: #e7e7e7;
    padding-top: 80px;
}

.row.justify-content-between {
    align-items: center;
    margin-bottom: 50px;
}

.foodImage img {
    width: 70%;
    height: 300px;
    border-radius: 10px;
}

.detailsOfFood {
    text-align: left;
    font-family: 'Poppins';
    margin-top: -50px;
}
.detailsOfFood h2.foodName{
    font-size: 40px;
    font-weight: 600;
    color: var(--text-color);
    margin-top: 15px;
    margin-bottom: 10px;
}
.detailsOfFood span.regularPrice{
    color: rgb(221, 129, 129);
    text-decoration: line-through;
    font-weight: 500;
    font-size: 28px;
    margin-right: 10px;
}
.detailsOfFood span.salePrice{
    color: var(--green);
    font-weight: 700;
    font-size: 32px;
}
.detailsOfFood button.viewBtn{
    color: white;
    background-color: var(--green);
    padding: 8px 35px;
    margin-bottom: 0px;
    border: 0px;
    border-radius: 0px;
    font-size: 20px;
    margin-top: 10px;
}

.detailsOfFood .foodRatting {
    color: var(--green);
    font-size: 22px;
    margin-left: 40px;
}

.detailsOfFood .quantity{
    border: 1px solid #eee;
    padding: 10px 7px;
    font-size: 20px;
    font-weight: 500;
    width: 50px;
    margin-top: 10px;
}
.detailsOfFood span.stock{
    color: var(--green);
    font-size: 18px;
}
.detailsOfFood span.cat{
    margin-left: 20px;
    color: var(--green);
    font-size: 18px;
}

.shortDesc {
    text-align: left;
    font-family: 'Poppins';
    border-radius: 5px;
    font-weight: 500;
    line-height: 27px;
    border: 1px solid #d3d3d3;
    padding: 15px 12px;
}

.shortDesc h2.descHeadline{
    color: var(--text-color);
}

/* Responsive food single card  */
@media only screen and (max-width: 768px){
.foodImage {
    margin-bottom: 70px;
}
}
@media only screen and (max-width: 425px){
.foodImage img {
    width: 95%;
    height: 330px;
}
}


h2.creditHeadline {
   color: #ccc;
   font-weight: 700;
   font-family: 'Poppins';
}
.portfolioLink {
    color: #bbb;
   text-decoration: none;
   transition: .3s;

}
.portfolioLink:hover {
   color: var(--green);
}
